.list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: rgb(215, 215, 221);
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.item {
    width: calc(100% / 5);
    display: flex;
    flex-direction: column;
}

.item:first-child {
    border-bottom-left-radius: 8px;
}

.item:last-child {
    border-bottom-right-radius: 8px;
}

